import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { Form, Input, Button, Select, Row, Col, Popover, Progress } from 'antd'
import { stringify } from 'qs'
import styles from './Register.less'

const FormItem = Form.Item
const {Option} = Select
const InputGroup = Input.Group

//
const passwordStatusMap = {
  ok: <div className={styles.success}></div>,
  pass: <div className={styles.warning}></div>,
  poor: <div className={styles.error}></div>,
}

const passwordProgressMap = {
  ok: 'success',
  pass: 'normal',
  poor: 'exception',
}

@Form.create()
class Register extends Component {
  constructor (props) {
    super(props)
    this.state = {
      count: 0,
      confirmDirty: false,
      visible: false,
      help: '',
      prefix: '86',
      submitting: false,
    }
  }

  componentWillUnmount () {
    clearInterval(this.interval)
  }

  handleComfirmBlur = (e) => {
    const {value} = e.target
    this.setState({confirmDirty: this.state.confirmDirty || !!value})
  }

  //
  onGetCaptcha = () => {
    let count = 59
    this.setState({count: count})
    this.interval = setInterval(() => {
      count -= 1
      this.setState({count: count})
      if (count === 0) {
        clearInterval(this.interval)
      }
    }, 1000)
  }

  //
  getPasswordStatus = () => {
    const {form} = this.props
    const value = form.getFieldValue('password')
    if (value && value.length > 9) {
      return 'ok'
    }
    if (value && value.length > 5) {
      return 'pass'
    }
    return 'poor'
  }

  //
  checkPassword = (rule, value, callback) => {
    //valueundefined!valuetrue!!valuefalse
    if (!value) {
      this.setState({
        help: '',
        visible: !!value,
      })
      callback('error')
    } else {
      this.setState({
        help: '',
      })
      if (!this.state.visible) {
        this.setState({
          visible: !!value,
        })
      }
      if (value.length < 6) {
        callback('error')
      } else {
        const {form} = this.props
        if (value && this.state.confirmDirty) {
          form.validateFields(['confirm'], {force: true})
        }
        callback()
      }
    }
  }

  //
  checkConfirm = (rule, value, callback) => {
    const {form} = this.props
    if (value && value !== form.getFieldValue('password')) {
      callback('')
    } else {
      callback()
    }
  }

  //
  changePrefix = (value) => {
    this.setState({
      prefix: value,
    })
  }

  //
  handleSubmit = (e) => {
    e.preventDefault()
    const {history} = this.props
    this.props.form.validateFields({force: true},
      (err, values) => {
        if (!err) {
          history.push({
            pathname: '/user/register-result',
            search: stringify({
              ...values,
              prefix: this.state.prefix,
            }),
          })
        }
      })
  }

  //
  renderPasswordProgress = () => {
    const {form} = this.props
    const value = form.getFieldValue('password')
    const passwordStatus = this.getPasswordStatus()
    return value && value.length ? (
      <div className={styles[`progress-${passwordStatus}`]}>
        <Progress
          status={passwordProgressMap[passwordStatus]}
          className={styles.progress}
          strokeWidth={6}
          percent={value.length * 10 > 100 ? 100 : value.length * 10}
          showInfo={false}
        />
      </div>
    ) : null
  }

  render () {
    const {form} = this.props
    const {count, help, visible, prefix, submitting} = this.state
    const {getFieldDecorator} = form
    return (
      <div className={styles.main}>
        <h3></h3>
        <Form onSubmit={this.handleSubmit}>
          <FormItem>
            {getFieldDecorator('mail', {
              rules: [
                {
                  required: true,
                  message: '',
                },
                {
                  type: 'email',
                  message: '',
                },
              ],
            })(<Input size="large" autoComplete="off" placeholder=""/>)}
          </FormItem>
          <FormItem help={help}>
            <Popover
              content={
                <div style={{padding: '4px 0'}}>
                  {passwordStatusMap[this.getPasswordStatus()]}
                  {this.renderPasswordProgress()}
                  <div style={{marginTop: '10px'}}>
                     6 
                  </div>
                </div>
              }
              overlayStyle={{width: 240}}
              placement="right"
              visible={visible}
            >
              {
                getFieldDecorator('password', {
                  rules: [
                    {
                      validator: this.checkPassword,
                    },
                  ],
                })(<Input size="large" autoComplete="off" type="password"
                          placeholder="6"/>)
              }
            </Popover>
          </FormItem>
          <FormItem>
            {
              getFieldDecorator('confirm', {
                rules: [
                  {
                    required: true,
                    message: '',
                  },
                  {
                    validator: this.checkConfirm,
                  },
                ],
              })(<Input size="large" type="password" autoComplete="off"
                        placeholder=""/>)
            }
          </FormItem>
          <FormItem>
            <InputGroup compact>
              <Select
                size="large"
                value={prefix}
                onChange={this.changePrefix}
                style={{width: '20%'}}
              >
                <Option value="86">+86</Option>
                <Option value="87">+87</Option>
              </Select>
              {
                getFieldDecorator('mobile', {
                  rules: [
                    {
                      required: true,
                      message: '',
                    },
                    {
                      pattern: /^1\d{10}$/,
                      message: '',
                    },
                  ],
                })(<Input size="large" autoComplete="off" style={{width: '80%'}}
                          placeholder="11"/>)
              }
            </InputGroup>
          </FormItem>
          <FormItem>
            <Row gutter={8}>
              <Col span={16}>
                {
                  getFieldDecorator('captcha', {
                    rules: [
                      {
                        required: true,
                        message: '',
                      },
                    ],
                  })(<Input size="large" autoComplete="off" placeholder=""/>)
                }
              </Col>
              <Col span={8}>
                <Button
                  size="large"
                  disabled={count}
                  className={styles.getCaptcha}
                  onClick={this.onGetCaptcha}
                >{count ? `${count} s` : ''}</Button>
              </Col>
            </Row>
          </FormItem>
          <FormItem>
            <Button
              size="large"
              loading={submitting}
              className={styles.submit}
              type="primary"
              htmlType="submit"
            ></Button>
            <Link className={styles.login} to="/user/login"></Link>
          </FormItem>
        </Form>
      </div>
    )
  }
}

export default Register
